import * as GSAP from "gsap";
import AbstractElement from "./AbstractElement";
import { CHANGE_STEP, TRANSITION_IN_COMPLETED } from "../actions/ElementActions";
import { CurrentUser, getRandomIds, getResultIdsByKey, getConstellations } from "../helper/ResultHelper";
import moment from "moment";

class LoadingElement extends AbstractElement {
    constructor(ref) {
        super(ref);

        this.timer = null;
        this.delayTime = 1;
        this.sequenceTotal = 30;

        this.info = this.ref.find(".stepper_info_inner");

        this.createImageSequence = this.createImageSequence.bind(this);
        this.preloadImages = this.preloadImages.bind(this);
        this.preloadCompleteHandler = this.preloadCompleteHandler.bind(this);

        this.transitionGraphics = this.transitionGraphics.bind(this);
        this.transitionInfo = this.transitionInfo.bind(this);
    }

    init() {
        this.reset();
    }

    reset() {
        const scaleRate = 5;
        GSAP.TweenMax.set(this.info, {
            scale: 2,
            alpha: 0,
        });
        super.reset();
    }

    createImageSequence() {
        if (CurrentUser.isFirstPlay) {
            CurrentUser.randomSequence = getRandomIds(this.sequenceTotal - 1);
            CurrentUser.currentId = Math.floor(Math.random() * 18 + 1);
            CurrentUser.isFirstPlay = false;
            CurrentUser.reRandom = false;
            CurrentUser.randomSequence.push(CurrentUser.currentId);
        } else {
            if (CurrentUser.reRandom) {
                CurrentUser.randomSequence = getRandomIds(this.sequenceTotal - 1);
                CurrentUser.currentId = Math.floor(Math.random() * 18 + 1);
                CurrentUser.randomSequence.push(CurrentUser.currentId);
            }
        }
        this.preloadImages(
            CurrentUser.randomSequence.map((item) => {
                return "./assets/images/result_" + item + ".png";
            }),
            this.preloadCompleteHandler
        );
    }

    preloadCompleteHandler(response) {
        if (this.timer) window.clearTimeout(this.timer);
        this.timer = window.setTimeout(() => {
            window.clearTimeout(this.timer);
            this.emitter.emit(CHANGE_STEP, { id: 2 });
        }, this.delayTime * 1000);
    }

    preloadImages(imgs, callback) {
        let loaded = 0;
        let images = [];
        imgs = Object.prototype.toString.apply(imgs) === "[object Array]" ? imgs : [imgs];
        let inc = function () {
            loaded += 1;
            if (loaded === imgs.length && callback) {
                callback(images);
            }
        };
        for (let i = 0; i < imgs.length; i++) {
            images[i] = new Image();
            images[i].onabort = inc;
            images[i].onerror = inc;
            images[i].onload = inc;
            images[i].src = imgs[i];
        }
    }

    transitionGraphics() {
        const duration = 1.2;
        GSAP.TweenMax.to(this.info, duration - 0.2, {
            // delay: 0.4,
            alpha: 1,
            scale: 1,
            onComplete: () => {
                this.createImageSequence();
            },
        });
    }

    transitionInfo() {}

    transitionIn() {
        this.transitionGraphics();
        super.transitionIn();
    }

    transitionHandler(type, id) {
        if (type === TRANSITION_IN_COMPLETED) {
        }
        super.transitionHandler(type, id);
    }
}

export default LoadingElement;
