import * as GSAP from "gsap";
import AbstractElement from "./AbstractElement";
import { initFacebookSDK, loginFB, getUser } from "../service/FacebookService";
import { CHANGE_STEP, TRANSITION_IN_COMPLETED, TRANSITION_OUT_COMPLETED } from "../actions/ElementActions";
import { CurrentUser } from "../helper/ResultHelper";

class WelcomeElement extends AbstractElement {
    constructor(ref) {
        super(ref);
        this.marqueeWrapper = this.ref.find(".marquee_wrapper");
        this.tools = this.ref.find(".stepper_tools");
        this.loginButton = this.ref.find(".loginButton");
        this.transitionInfo = this.transitionInfo.bind(this);
    }

    init() {
        this.reset();
        this.loginButton.on("click tap", (e) => {
            this.emitter.emit(CHANGE_STEP, { id: 1 });
        });
    }

    reset() {
        this.ref.find(".marquee").removeClass("active");
        GSAP.TweenMax.set(this.loginButton, {
            alpha: 0,
        });
        this.tools.removeClass("active");
        super.reset();
    }
    remove() {
        this.loginButton.off("click tap");
    }
    transitionInfo() {
        GSAP.TweenMax.to(this.loginButton, 0.8, {
            alpha: 1,
            onComplete: () => {
                this.tools.addClass("active");
                this.ref.find(".marquee").addClass("active");
            },
        });
    }
    transitionHandler(type, id) {
        if (type === TRANSITION_IN_COMPLETED) {
            this.transitionInfo();
        }
        if (type === TRANSITION_OUT_COMPLETED) {
            this.reset();
        }
        super.transitionHandler(type, id);
    }
}

export default WelcomeElement;
