import * as GSAP from "gsap";
import EventEmitter from "eventemitter3";
import { TRANSITION_IN_COMPLETED, TRANSITION_OUT_COMPLETED } from "../actions/ElementActions";

class AbstractElement {
    constructor(ref) {
        this._ref = ref;
        this.emitter = new EventEmitter();

        this.init = this.init.bind(this);
        this.reset = this.reset.bind(this);
        this.remove = this.remove.bind(this);
        this.transitionIn = this.transitionIn.bind(this);
        this.transitionOut = this.transitionOut.bind(this);
        this.transitionHandler = this.transitionHandler.bind(this);
    }

    init() {
        console.log("AbstractElement.init");
    }

    reset() {
        GSAP.TweenMax.set(this.ref, {
            scale: 1,
        });
    }

    remove() {}

    transitionIn() {
        this.ref.css("display", "block");
        GSAP.TweenMax.to(this.ref, 0, {
            alpha: 1,
            onUpdate: () => {},
            onComplete: () => {
                this.transitionHandler(TRANSITION_IN_COMPLETED, null);
            },
        });
    }

    transitionOut(id) {
        GSAP.TweenMax.to(this.ref, 0, {
            alpha: 0,
            ease: GSAP.Power3.easeOut,
            onComplete: () => {
                this.ref.css("display", "none");
                this.transitionHandler(TRANSITION_OUT_COMPLETED, id);
            },
        });
    }

    transitionHandler(type, id) {
        if (type === TRANSITION_IN_COMPLETED) {
        }
        if (type === TRANSITION_OUT_COMPLETED) {
            this.reset();
            this.remove();
        }
        this.emitter.emit(type, {
            action: type,
            id: id,
        });
    }

    get ref() {
        return this._ref;
    }
}

export default AbstractElement;
