export const getResultIdsByKey = (key) => {
    const currentData = resultData.find((item) => {
        return item.name === key;
    });
    return shuffle([...currentData.resultIds]);
};

export const shuffle = (array) => {
    let currentIndex = array.length,
        temporaryValue,
        randomIndex;

    // While there remain elements to shuffle...
    while (0 !== currentIndex) {
        // Pick a remaining element...
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex -= 1;

        // And swap it with the current element.
        temporaryValue = array[currentIndex];
        array[currentIndex] = array[randomIndex];
        array[randomIndex] = temporaryValue;
    }

    return array;
};

export const getRandomIds = (total) => {
    let nums = [],
        maxNum = 18,
        num;
    while (nums.length < total) {
        num = Math.round(Math.random() * (maxNum - 1) + 1);
        if (nums.length < maxNum) {
            if (nums.indexOf(num) === -1) {
                nums.push(num);
            }
        } else {
            nums.push(num);
        }
    }
    return nums;
};
export const getConstellations = (m, d) => {
    return "".substr(
        m * 2 - (d < "102223444433".charAt(m - 1) - -19) * 2,
        2
    );
};
