import * as GSAP from "gsap";
import AbstractElement from "./AbstractElement";
import { CurrentUser } from "../helper/ResultHelper";
import { CHANGE_STEP, TRANSITION_IN_COMPLETED, TRANSITION_OUT_COMPLETED } from "../actions/ElementActions";
import { shareFB } from "../service/FacebookService";

class ResultElement extends AbstractElement {
    constructor(ref) {
        super(ref);
        this.repeatButton = this.ref.find(".repeatButton");
        this.shareButton = this.ref.find(".shareButton");
        this.tools = this.ref.find(".stepper_tools");
        this.cover = this.ref.find(".stepper_cover_wrapper");
        this.marqueeWrapper = this.ref.find(".marquee_translation");
        this.createCover = this.createCover.bind(this);
        this.swapResultCover = this.swapResultCover.bind(this);
        this.transitionGraphics = this.transitionGraphics.bind(this);
        this.transitionInfo = this.transitionInfo.bind(this);

        this.urlList = [
            "https://www.nmhshop.com.tw/category.html?id=61", // 0
            "https://www.nmhshop.com.tw/category.html?id=61", // 1
            "https://www.nmhshop.com.tw/category.html?id=61", // 2
            "https://www.nmhshop.com.tw/category.html?id=61", // 3
            "https://www.nmhshop.com.tw/category.html?id=61", // 4
            "https://www.nmhshop.com.tw/category.html?id=61", // 5
            "https://www.nmhshop.com.tw/category.html?id=61", // 6
            "https://www.nmhshop.com.tw/category.html?id=61", // 7
            "https://www.nmhshop.com.tw/category.html?id=61", // 8
            "https://www.nmhshop.com.tw/category.html?id=61", // 9
            "https://www.nmhshop.com.tw/category.html?id=61", // 10
            "https://www.nmhshop.com.tw/product.html?id=513", // 11
            "https://www.nmhshop.com.tw/product.html?id=513", // 12
            "https://www.nmhshop.com.tw/category.html?id=61", // 13
            "https://www.nmhshop.com.tw/category.html?id=61", // 14
            "https://www.nmhshop.com.tw/product.html?id=511", // 15
            "https://www.nmhshop.com.tw/product.html?id=61", // 16
            "https://www.nmhshop.com.tw/product.html?id=512", // 17
            "https://www.nmhshop.com.tw/category.html?id=61", // 18
        ];
    }

    init() {
        this.reset();
        this.repeatButton.on("click tap", (e) => {
            if (CurrentUser.repeatCount < CurrentUser.repeatTotal - 1) {
                CurrentUser.repeatCount++;
            } else {
                CurrentUser.reRandom = true;
            }
            this.emitter.emit(CHANGE_STEP, { id: 1 });
        });

        const href = window.location.href;
        const path = href.substring(0, href.lastIndexOf("/"));
        const shareUrl =
            "https://www.facebook.com/sharer/sharer.php?u=" + path + "/share/share" + CurrentUser.currentId + ".html";
        this.shareButton.attr("href", shareUrl);
        this.shareButton.attr("target", "_blank");
        console.log("/share/share" + CurrentUser.currentId + ".html", this.urlList[CurrentUser.currentId]);
        this.cover.find("a").attr("href", this.urlList[CurrentUser.currentId]);
    }

    remove() {
        this.repeatButton.off("click tap");
        this.shareButton.off("click tap");
    }

    reset() {
        this.ref.find(".marquee").removeClass("active");
        const scaleRate = 3;
        GSAP.TweenMax.set(this.marqueeWrapper, {
            scale: scaleRate,
            alpha: 0,
            transformOrigin: "50% 50%",
        });
        GSAP.TweenMax.set(this.cover, {
            scale: 2,
            alpha: 0,
            transformOrigin: "50% 50%",
        });
        GSAP.TweenMax.set(this.tools, {
            alpha: 0,
        });
        this.tools.removeClass("active");
        super.reset();
    }

    createCover() {
        let count = { x: 0 };
        GSAP.TweenMax.to(count, 4, {
            x: CurrentUser.randomSequence.length - 1,
            ease: GSAP.Quad.easeOut,
            onUpdate: () => {
                this.swapResultCover(Math.floor(count.x));
            },
            onComplete: () => {
                this.transitionInfo();
                this.ref.find(".marquee").addClass("active");
            },
        });
    }

    swapResultCover(index) {
        const currentPath = "./assets/images/result_" + CurrentUser.randomSequence[index] + ".png";
        this.ref.find(".stepper_cover").find("img").attr("src", currentPath);
    }

    transitionGraphics() {
        const duration = 1.2;
        let count = { x: 0 };
        GSAP.TweenMax.to(count, 0.5, {
            x: 10,
            ease: GSAP.Quart.easeOut,
            onComplete: () => {
                this.createCover();
            },
        });
        GSAP.TweenMax.to(this.marqueeWrapper, duration, {
            alpha: 1,
            scale: 1,
            transformOrigin: "50% 50%",
            ease: GSAP.Quart.easeOut,
            onComplete: () => {},
        });
        GSAP.TweenMax.to(this.cover, duration - 0.2, {
            delay: 0.3,
            alpha: 1,
            scale: 1,
            transformOrigin: "50% 50%",
            ease: GSAP.Quart.easeOut,
        });
    }

    transitionInfo() {
        GSAP.TweenMax.to(this.tools, 0.5, {
            alpha: 1,
            onComplete: () => {
                this.tools.addClass("active");
            },
        });
    }

    transitionIn() {
        this.transitionGraphics();
        super.transitionIn();
    }

    transitionHandler(type, id) {
        if (type === TRANSITION_IN_COMPLETED) {
        }
        if (type === TRANSITION_OUT_COMPLETED) {
            this.reset();
        }
        super.transitionHandler(type, id);
    }
}

export default ResultElement;
