import "./PolyFills";
import * as $ from "jquery";
import WelcomeElement from "./elements/WelcomeElement";
import LoadingElement from "./elements/LoadingElement";
import ResultElement from "./elements/ResultElement";
import { TRANSITION_IN_COMPLETED, TRANSITION_OUT_COMPLETED, CHANGE_STEP } from "./actions/ElementActions";

$(function () {
    createElements(stepId);
    $(window).resize(onWindowResize);
    onWindowResize();
});

function onWindowResize() {
    if (window.innerWidth >= 1200) {
        let distance = 50;
        $("#textPath").attr(
            "d",
            getRoundedRectPath(
                distance,
                distance,
                window.innerWidth - distance * 2,
                window.innerHeight - distance * 2,
                40
            )
        );
    }
}
function getRoundedRectPath(x, y, w, h, r) {
    return `M${x + r},${y} h${w - 2 * r} a${r},${r} 0 0 1 ${r},${r} v${h - 2 * r} a${r},${r} 0 0 1 -${r},${r} h${
        2 * r - w
    } a-${r},-${r} 0 0 1 -${r},-${r} v${2 * r - h} a${r},${r} 0 0 1 ${r},-${r} z`;
}

/*
 * Elements
 * */
let stepId = 0;
let currentElement = null;
const elementData = [
    { id: "#stepWelcome", class: WelcomeElement, target: null },
    { id: "#stepLoading", class: LoadingElement, target: null },
    { id: "#stepResult", class: ResultElement, target: null },
];

function createElements(id) {
    if (currentElement) {
        currentElement.transitionOut(id);
        return;
    }
    stepId = id;
    const CurrentClass = elementData[stepId].class;
    currentElement = new CurrentClass($(elementData[stepId].id));
    currentElement.emitter.on(CHANGE_STEP, changeStepHandler);
    currentElement.emitter.on(TRANSITION_IN_COMPLETED, transitionHandler);
    currentElement.emitter.on(TRANSITION_OUT_COMPLETED, transitionHandler);
    currentElement.init();
    currentElement.transitionIn();
}

function changeStepHandler(param) {
    createElements(param.id);
}

function transitionHandler(param) {
    if (param.action === TRANSITION_IN_COMPLETED) {
    }
    if (param.action === TRANSITION_OUT_COMPLETED) {
        currentElement = null;
        createElements(param.id);
    }
}
